.container {
  min-height: 100vh;
  padding: 0px max(calc(100% - 500px) / 2, 0px);
  background-color: #cec3bd;
}

.contentContainer {
  background-color: #faf5f2;
  min-height: 100%;
  width: 100%;
  padding: 35px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  gap: 70px 0px;
}

.dateContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;

  font-family: "Cormorant Infant", serif;
  letter-spacing: 3px;
  line-height: normal;
  font-size: 76px;
  font-weight: 400;
}

.dateContainer .dot {
  font-size: 48px;
  line-height: 0px;
}

.namesContainer {
  display: flex;
  flex-direction: column;
  align-items: center;

  font-size: 64px;
  line-height: 46px;

  font-family: "Cormorant Garamond", serif;

  font-weight: 300;
  line-height: 48px;
}

.entrenceContainer {
  display: flex;
  flex-direction: column;
  gap: 10px 0px;
  align-items: center;
  justify-content: center;
}

.entrenceLogo {
  border-radius: 16px;
}

.entrenceText {
  text-align: center;

  font-size: 24px;
  line-height: 30px;

  font-family: "EB Garamond", serif;
  font-weight: 400;
  margin-bottom: 10px;
}

.locationContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;

  text-align: center;
  font-family: "EB Garamond", serif;
}

.locationContainer .header {
  font-size: 64px;
  line-height: 60px;

  font-weight: 400;
  padding: 10px;
}

.text {
  font-size: 24px;
  line-height: 29px;
}

.locationSubSection {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 10px;
}

.dressSectionContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;

  text-align: center;
  font-family: "EB Garamond", serif;
  font-size: 24px;

  margin-bottom: 24px;
}

.dressSectionContainer .header {
  font-size: 64px;
  line-height: 60px;

  font-weight: 400;
  padding: 10px 0px 35px;
}

.colorsContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.detailsContainer {
  display: flex;
  flex-direction: column;
  gap: 10px;

  text-align: center;
  font-family: "EB Garamond", serif;
  font-size: 24px;

  margin-bottom: 24px;
}

.detailsContainer .header {
  font-size: 64px;
  line-height: 60px;

  font-weight: 400;
  padding: 10px 0px 35px;
}

.presentPhotoConainer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
}

.presentPhotoConainer img {
  height: 200px;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "EB Garamond", serif;
  font-size: 20px;
  border: none;

  padding: 8px 16px;
  background: #2aabee;
  color: white;
  border-radius: 16px;
  width: max-content;
  align-self: center;
  cursor: pointer;
  transition: all 0.2s linear;
}

.button:hover {
  background: #229ed9;
}
